import './App.css';
import { lazy, Suspense, createContext, useState, useEffect } from 'react';
import { Navbar, Container, Nav } from 'react-bootstrap';
import { Routes, Route, Link, useNavigate } from 'react-router-dom';

import bg from './bg.png';
import base from './data';
import Main from './pages/Main';
import axios from 'axios';
import { useQuery } from '@tanstack/react-query';

//       
const Detail = lazy(() => import('./pages/Detail.js'))
const Cart = lazy(() => import('./pages/Cart.js'))
const Event = lazy(() => import('./pages/Event'))
const LightHouse = lazy(() => import('./components/LightHouse.js'))


export let Context1 = createContext();


function App() {

  let [data, setData] = useState(base);
  let [, set] = useState([10, 11, 12]);

  let navigate = useNavigate();

  // 
  useEffect(() => {

    let duplication = localStorage.getItem('watched');
    if (!duplication) {
      localStorage.setItem('watched', JSON.stringify([]))
    }

  }, [])

  // react-query
  let result = useQuery(['user'], () =>
    axios.get('https://codingapple1.github.io/userdata.json')
      .then((data) => data.data)
  )

  // result.data
  // result.isLoading
  // result.error

  return (
    <div className="App">

      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand onClick={() => {
            navigate('/')
          }}>Jammall</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => {
              navigate('/')
            }}>Home</Nav.Link>
            <Nav.Link onClick={() => {
              navigate('/cart')
            }}>Cart</Nav.Link>
          </Nav>
          <Nav className='ms-auto'>
            {result.isLoading && '..'}
            {result.data && result.data.name}
            {result.error && ''}
          </Nav>
        </Container>
      </Navbar>

      {/*   html  lazy   */}
      <Suspense fallback={<div>...</div>}>
        <Routes>
          <Route path='/' element={<Main bg={bg} data={data} setData={setData} />} />

          <Route path='/detail/:id' element={
            <Context1.Provider value={{  }}>
              <Detail data={data} />
            </Context1.Provider>
          } />

          <Route path='/cart' element={<Cart />} />

          <Route path='/event' element={<Event />}>
            <Route path='one' element={<p>   </p>} />
            <Route path='two' element={<p> </p>} />
          </Route>

          <Route path='/lighthouse' element={<LightHouse />} />

          <Route path='*' element={
            <div>
              .
            </div>
          } />

        </Routes>
      </Suspense>


    </div>
  );
}

export default App;
