import { createSlice } from "@reduxjs/toolkit";

let user = createSlice({ //
  name: 'user', //
  initialState: { name: 'kang', age: 25 }, //
  reducers: {
    changeName(state) {
      state.name = 'minji' //useImmer    
    },
    userIncrease(state, i) {
      state.age += i.payload //   payload
    }
  }
})

export let { changeName, userIncrease } = user.actions;

export default user;
